*{font-family:"Be Vietnam Pro",sans-serif}h1{color:#ff0000}a{color:unset;text-decoration:none}.section__padding{padding:3.5rem 10rem}.section__margin{margin:3.5rem 10rem}

.navbar{display:flex;justify-content:space-between;align-items:center}.navbar__menu{display:flex;list-style:none}.navbar__menu a{color:#242d52;font-size:14px;font-weight:500;transition:color 200ms ease-in}.navbar__menu a:hover{color:#9095a7}.navbar__menu a:not(:last-child){margin-right:2rem}

.btn{background:#f25f3a;color:#ffefeb;padding:1rem 2rem;font-size:14px;font-weight:500;border:none;border-radius:10rem;box-shadow:0px 4px 10px 2px rgba(243,97,60,0.3);cursor:pointer;transition:opacity 200ms ease-in}.btn:hover{opacity:0.7}

